.card {
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 24px;
    padding: 24px;
    border-radius: 40px;
    background-color: #1c1c21;
    box-sizing: border-box;
}

.caption {
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}

.date {
    display: flex;
    flex-direction: row;
    gap: 9.5px;
}

.status_done {
    color: #0cc;
}

.status_pending {
    color: #f2f2f3;
}

.box {
    display: flex;
    flex-direction: row;
    gap: 24px;
    justify-content: space-between;
}

.ingredients {
    max-width: 436px;
    display: flex;
    padding: 0;
}

.img_background {
    margin-left: -15px;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 64px;
    height: 64px;
    position: relative;
    background: linear-gradient(45deg, #801ab2, #4c4cff);
    border-radius: 50%;
}

.counter {
    color: #f2f2f3;
    z-index: 2;
    transform: translate(-43px, 20px);
}

.img_container {
    width: 60px;
    height: 60px;
    background-color: #000000;
    border-radius: 50%;
}

.img {
    width: 60px;
    height: 60px;
    object-fit: cover;
    transform: scale(0.9);
}

.price {
    display: flex;
    flex-direction: row;
    gap: 8px;
}

.element {
    display: flex;
    flex-direction: column;
    gap: 8px;
}
